.settings {
  position: relative;
  min-width: 275px;
  padding: 1em;
  background: #fff;
  outline: none;
  -webkit-transition: all .4s;
  -o-transition: all .4s;
  transition: all .4s;
  overflow: scroll;
}

.settings .slider {
  width: 100% !important;
}



